import { useState } from 'react';
import { ShowIncrement } from './ShowIncrement';


const CallbackHook = () => {
    
    const [counter, setCounter] = useState(10)
    const incerment = () =>{
        setCounter( counter + 1)
    }
    
    
    
    return( 
        <div>
            <h1>useCallback Hook</h1>
            <hr />
            <h3>{counter}</h3>
            <ShowIncrement increment={ incerment } />
        </div>
    );
}

export{
    CallbackHook
}